.gutenberghub-tab-button {
	position: relative;

	.gutenberghub-tab-collapsible {
		overflow: hidden;
		position: relative;

		transition: 0.3s all ease-in-out;
		&.gutenberghub-tab-collapsible-initialized {
			.gutenberghub-tab-collapsible-inner-container {
				opacity: 0;
				position: absolute;
				z-index: -1;
			}
		}
	}
}
